#article {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @mobile-min1920{
    padding: 30px 100px 30px 50px;
  }
  @media @mobile-max1920{
    padding: 50px 50px 30px 50px;
  }
  @media @mobile-max1440{
    padding: 20px 50px 30px 50px;
  }
  @media @mobile-max1366{
    padding: 16px 60px 30px 50px;
  }
  @media @mobile-max1280{
    padding: 50px 50px 30px 50px;
  }

  @media @mobile-max1024-landscape{
    padding: 20px 70px 20px 50px;
  }
  @media @mobile-max1024-portrait{
    padding: 20px 20px 20px 20px;
  }

  @media @mobile-max768-landscape{
    padding: 20px 20px 20px 20px;
  }
  @media @mobile-min568-max768{
    padding: 20px 20px 20px 20px;
  }
  @media @mobile-max568-portrait{
    padding: 20px 20px 20px 20px;
  }
  @media @mobile-max480{
    padding: 20px 20px 20px 20px;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .left-side > .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 60px 0 0 0;

    .article-header {
      display: block;

      @media @mobile-min568-max768{
        margin-top: -50px;
      }

      .backward {
        background: #ff7d00;
        text-decoration: none;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        padding: 4px;
        z-index: 4;
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        justify-content: center;
        position: relative;

        @media @mobile-min1920{
          top: 85px;
        }
        @media @mobile-max1920{
          top: 80px;
        }
        @media @mobile-max1440{
          top: 72px;
        }
        @media @mobile-max1366{
          top: 72px;
        }
        @media @mobile-max1280{
          top: 72px;
        }

        @media @mobile-max1024-landscape{
          top: 56px;
        }
        @media @mobile-max1024-portrait{

        }

        @media @mobile-max768-landscape{

        }
        @media @mobile-min568-max768{
          top: 52px;
        }
        @media @mobile-max568-portrait{

        }
        @media @mobile-max480{

        }

        a {
          color: #fff;
          text-decoration: none;
        }
      }

      .headline {
        color: #249fbe;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        line-height: 1.2;
        flex-direction: column;
        z-index: 3;
        -webkit-box-flex: 2;
        -ms-flex: 2;
        flex: 2;

        @media @mobile-min1920{
          max-width: 56%;
          font-size: 70px;
          margin-bottom: 50px;
          margin-left: 230px;
        }
        @media @mobile-max1920{
          font-size: 62px;
          margin-bottom: 30px;
          margin-left: 150px;
          max-width: 56%;
        }
        @media @mobile-max1440{
          font-size: 52px;
          margin-bottom: 30px;
          margin-left: 160px;
          max-width: 56%;
        }
        @media @mobile-max1366{
          font-size: 48px;
          margin-bottom: 30px;
          margin-left: 146px;
          max-width: 56%;
        }
        @media @mobile-max1280{
          font-size: 48px;
          margin-bottom: 30px;
          margin-left: 146px;
          max-width: 56%;
        }

        @media @mobile-max1024-landscape{
          font-size: 36px;
          margin-bottom: 30px;
          margin-left: 58px;
          max-width: 50%;
        }
        @media @mobile-max1024-portrait{
          font-size: 2em;
        }

        @media @mobile-max768-landscape{
          font-size: 2em;
        }
        @media @mobile-min568-max768{
          font-size: 1.4em;
          margin-left: 40px;
          max-width: 90%;
        }
        @media @mobile-max568-portrait{
          font-size: 2em;
        }
        @media @mobile-max480{
          font-size: 1.4em;
        }

      }
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      height: 100%;

      @media @mobile-min1920{
        padding: 40px 20px 20px 20px;
      }
      @media @mobile-max1920{
        padding: 0 20px 20px 20px;
      }
      @media @mobile-max1440{
        padding: 0 20px 20px 20px;
      }
      @media @mobile-max1366{
        padding: 20px 20px 20px 20px;
      }
      @media @mobile-max1280{
        padding: 50px 20px 20px 20px;
      }

      @media @mobile-max1024-landscape{
        padding: 20px 0 20px 0;
      }
      @media @mobile-max1024-portrait{
        padding: 40px 20px 0 20px;
      }

      @media @mobile-max768-landscape{
        padding: 0;
      }
      @media @mobile-min568-max768{
        padding: 0;
      }
      @media @mobile-max568-portrait{
        padding: 0;
      }
      @media @mobile-max480{
        padding: 0;
      }


      section .content {
        padding: 0;
      }

      .content {
        //display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: flex-start;

        article {
          display: flex;
          align-items: flex-start;
          flex-shrink: 1;
          padding: 0;

          @media @mobile-min1920{

          }
          @media @mobile-max1920{

          }
          @media @mobile-max1440{

          }
          @media @mobile-max1366{

          }
          @media @mobile-max1280{

          }

          @media @mobile-max1024-landscape{

          }
          @media @mobile-max1024-portrait{
            overflow-y: scroll;
            flex-direction: column;
            display: block;
          }

          @media @mobile-max768-landscape{
            overflow-y: scroll;
            flex-direction: column;
            display: block;
          }
          @media @mobile-min568-max768{
            overflow-y: scroll;
            flex-direction: column;
            display: block;
          }
          @media @mobile-max568-portrait{
            overflow-y: scroll;
            flex-direction: column;
            display: block;
          }
          @media @mobile-max480{
            overflow-y: scroll;
            flex-direction: column;
            display: block;
          }

          .article-left, .article-right {
            display: flex;
          }

          .date {
            flex: 1;
            color: #b1ccd2;

            @media @mobile-max1024-portrait{
              display: block;
              margin-bottom: 10px;
            }

            @media @mobile-max768-landscape{
              display: block;
              margin-bottom: 10px;
            }
            @media @mobile-min568-max768{
              display: block;
              margin-bottom: 10px;
            }
            @media @mobile-max568-portrait{
              display: block;
              margin-bottom: 10px;
            }
            @media @mobile-max480{
              display: block;
              margin-bottom: 10px;
            }

          }

          .image {
            flex: 2;

            .video-container {
              float: none;
              clear: both;
              width: 100%;
              position: relative;
              padding-bottom: 56.25%;
              padding-top: 25px;
              height: 0;

              iframe {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
              }

              //@media @mobile {
              //  align-items: center;
              //  min-height: 100%;
              //  width: 100%;
              //}

            }

            //@media @mobile {
            //  img {
            //    width: 100%;
            //    display: block;
            //  }
            //}
          }

          .content {
            flex: 4;
            padding: 0;
            flex-wrap: nowrap;
            justify-content: inherit;
            font-family: 'Proxima Nova';
            color: #525a5d;
            position: relative;

            @media @mobile-min1920{
              max-height: 352px;
              margin-left: 60px;
            }
            @media @mobile-max1920{
              max-height: 352px;
              margin-left: 60px;
            }
            @media @mobile-max1440{
              max-height: 328px;
              margin-left: 60px;
            }
            @media @mobile-max1366{
              max-height: 328px;
              margin-left: 60px;
            }
            @media @mobile-max1280{
              max-height: 328px;
              margin-left: 60px;
            }

            @media @mobile-max1024-landscape{
              max-height: 364px;
              margin-left: 60px;
            }
            @media @mobile-max1024-portrait{
              margin-top: 20px;
            }

            @media @mobile-max768-landscape{
              margin-top: 20px;
            }
            @media @mobile-min568-max768{
              margin-top: 20px;
            }
            @media @mobile-max568-portrait{
              margin-top: 20px;
            }
            @media @mobile-max480{
              margin-top: 20px;
            }

            p {
              margin: 7px 0;

              &:first-child {
                margin-top: 0;
              }

              b {
                color: black;
                font-style: italic;
              }
            }

            ol {
              list-style: none;
              margin: 0;

              li:before {
                @top-margin: 20px;
                margin: @top-margin 0;
                background: #b0d3de;
                content: ' ';
                width: 8px;
                height: 8px;
                display: inline-block;
                font-style: italic;
                border-radius: 50%;
                margin: @top-margin 10px 0 0; /* Расстояние от маркера до текста */
              }
            }
          }
        }
      }

    }
  }
}
