footer.footer {
  display: flex;
  align-items: center;

  @media @mobile-max1024-portrait{
    flex-direction: column;
    align-items: flex-start !important;
    z-index: 1;
    margin-top: 38px;
    min-height: 60px;
    padding: 0 10px 5px 10px;
  }

  @media @mobile-max768-landscape{
    flex-direction: column;
    align-items: flex-start !important;
    z-index: 1;
    margin-top: 0;
    min-height: 60px;
    padding: 0 10px 5px 10px;
  }
  @media @mobile-min568-max768{
    flex-direction: column;
    align-items: flex-start !important;
    z-index: 1;
    margin-top: 0;
    min-height: 55px;
    padding: 0 10px 5px 10px;
  }
  @media @mobile-max568-portrait{
    flex-direction: column;
    align-items: flex-start !important;
    z-index: 1;
    margin-top: 0;
    min-height: 60px;
    padding: 0 10px 5px 10px;
  }
  @media @mobile-max480{
    flex-direction: column;
    align-items: flex-start !important;
    z-index: 1;
    margin-top: 0;
    min-height: 60px;
    padding: 16px 0 0 0;
  }

  .copyright {
    flex: 3;
    font-family: 'Proxima Nova';
    font-size: 14px;
    color: #9db7c1;
    letter-spacing: 0.025em;

    @media @mobile-max1024-portrait{
      display: none;
    }

    @media @mobile-max768-landscape{
      display: none;
    }
    @media @mobile-min568-max768{
      display: none;
    }
    @media @mobile-max568-portrait{
      display: none;
    }
    @media @mobile-max480{
      display: none;
    }

  }

  .copyright-mobile {
    font-family: 'Proxima Nova';

    @media @mobile-min1920{
      display: none;
    }
    @media @mobile-max1920{
      display: none;
    }
    @media @mobile-max1440{
      display: none;
    }
    @media @mobile-max1366{
      display: none;
    }
    @media @mobile-max1280{
      display: none;
    }

    @media @mobile-max1024-landscape{
      display: none;
    }
    @media @mobile-max1024-portrait{
      order: 1;
      display: block;
      font-size: 14px;
      color: #9db7c1;
    }

    @media @mobile-max768-landscape{
      order: 1;
      display: block;
      font-size: 14px;
      color: #9db7c1;
    }
    @media @mobile-min568-max768{
      order: 1;
      display: block;
      font-size: 14px;
      color: #9db7c1;
    }
    @media @mobile-max568-portrait{
      order: 1;
      display: block;
      font-size: 14px;
      color: #9db7c1;
    }
    @media @mobile-max480{
      order: 1;
      display: block;
      font-size: 11px;
      margin-left: 5px;
      color: #9db7c1;
    }

  }

  .social {
    flex: 1;
    display: flex;
    align-items: center;

    @media @mobile-max568-portrait{
      order: 0;
    }


    @media @mobile-max768-landscape{
      order: 0;
    }
    @media @mobile-min568-max768{
      order: 0;
    }
    @media @mobile-max568-portrait{
      order: 0;
    }
    @media @mobile-max480{
