    }

    .box {
      margin: 8px;

      a {
        text-decoration: none;
        color: #1d9dbd;
        font-size: 15px;

        @media @mobile-max568-portrait{
          &, &:visited {
            text-decoration: none;
            color: #1d9dbd ;
          }
        }

        @media @mobile-max768-landscape{
          &, &:visited {
            text-decoration: none;
            color: #1d9dbd ;
          }

          &:first-child {
            margin-left: 0;
          }
        }
        @media @mobile-min568-max768{
          &, &:visited {
            text-decoration: none;
            color: #1d9dbd ;
          }

          &:first-child {
            margin-left: 0;
          }
        }
        @media @mobile-max568-portrait{
          &, &:visited {
            text-decoration: none;
            color: #1d9dbd ;
          }

          &:first-child {
            margin-left: 0;
          }
        }
        @media @mobile-max480{
          &, &:visited {
            text-decoration: none;
            color: #1d9dbd ;
          }

          &:first-child {
            margin-left: 0;
          }
        }

      }
    }
  }

  .cta {
    padding-left: 40px;
    font-family: 'Brandon Bold';
    font-size: 14px;
    color: #1d9dbd;
    text-transform: uppercase;
    letter-spacing: 0.025em;
    display: none;

    @media @mobile-max568-portrait{
