    &:before {
      content: ' ';
      height: 1px;
      position: absolute;
      background: #cadbe0;
      z-index: -1;

      @media @mobile-max568-portrait{
        width: 20em;
        top: 2em;
        left: -25%;
      }

      @media @mobile-max768-landscape{
        width: 20em;
        top: 2em;
        left: -25%;
      }
      @media @mobile-min568-max768{
        width: 20em;
        top: 2em;
        left: -25%;
      }
      @media @mobile-max568-portrait{
