#about {
  height: 100%;
  background: url('../images/backgrounds/about.jpg');
  background-size: cover;
  background-color: #ecf8f9;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;

  @media @mobile-min1920{
    padding: 30px 100px 30px 50px;
  }
  @media @mobile-max1920{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1440{
    padding: 50px 50px 30px 50px;
  }
  @media @mobile-max1366{
    padding: 50px 60px 30px 50px;
  }
  @media @mobile-max1280{
    padding: 50px 50px 30px 50px;
  }

  @media @mobile-max1024-landscape{
    padding: 30px 70px 20px 50px;
    background-position: 59% -30%;
    background-size: 139%;
  }
  @media @mobile-max1024-portrait{
    background-position: 48% 0;
    background-size: 262%;
    padding: 20px 20px 20px 20px;
  }

  @media @mobile-max768-landscape{
    padding: 10px 20px 10px 20px;
    background-position: 61% 52%;
    background-size: 129%;
  }
  @media @mobile-min568-max768{
    padding: 10px 20px 10px 20px;
    background-position: 61% 52%;
    background-size: 129%;
  }
  @media @mobile-max568-portrait{
    padding: 35px 20px 20px 20px;
    background-position: 26% -20%;
    background-size: 235%;
  }
  @media @mobile-max480{
    padding: 20px 20px 20px 20px;
    background-position: 29% -17%;
    background-size: 228%;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;
    min-height: 100%;

    @media @mobile-max768-landscape{
      padding-top: 10%;
    }
    @media @mobile-min568-max768{
      padding-top: 10%;
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      .page-content {
        display: flex;
        padding-top: 280px;
        & > * {
          flex: 1;
        }

        .left-side {
          @media @mobile-max768-landscape{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
          }
          @media @mobile-min568-max768{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
          }
          @media @mobile-max568-portrait{
            display: none;
          }
          @media @mobile-max480{
            display: none;
          }
        }

        .right-side {
          display: flex;
          flex-direction: column;

          @media @mobile-min1920{
            //margin-top: 16%;
          }
          @media @mobile-max1920{
            //margin-top: 20%;
          }
          @media @mobile-max1440{
            margin-top: 20%;
          }
          @media @mobile-max1366{
            margin-top: 20%;
          }
          @media @mobile-max1280{
            margin-top: 40%;
          }

          @media @mobile-max1024-landscape{
            margin-top: 26%;
          }
          @media @mobile-max1024-portrait{
            margin-top: 40%;
          }

          @media @mobile-max768-landscape{
          }

          @media @mobile-min568-max768{
          }

          @media @mobile-max568-portrait{
            margin-top: 92%;
          }
          @media @mobile-max480{
            margin-top: 88%;
          }

          .spans {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            line-height: 1.2;

            @media @mobile-max768-landscape{
              margin-top: 0;
            }

            span {
              color: #525a5d;
              font-family: 'Proxima Nova';

              @media @mobile-min1920{
                width: 500px;
                margin-left: 160px;
                margin-top: 2%;
                font-size: 1.2em;
                line-height: 1.4em;
              }
              @media @mobile-max1920{
                width: 500px;
                margin-left: 90px;
                margin-top: 2%;
                font-size: 1.2em;
                line-height: 1.4em;
              }
              @media @mobile-max1440{
                width: 500px;
                margin-left: 90px;
                margin-top: 2%;
                font-size: 1.2em;
                line-height: 1.4em;
              }
              @media @mobile-max1366{
                width: 500px;
                margin-left: 90px;
                margin-top: 2%;
                font-size: 1.2em;
                line-height: 1.4em;
              }
              @media @mobile-max1280{
                width: 500px;
                margin-top: 2%;
                font-size: 1.2em;
                line-height: 1.4em;
              }

              @media @mobile-max1024-landscape{
                margin-top: 3%;
                font-size: 1.1em;
                line-height: 1.3em;
              }
              @media @mobile-max1024-portrait{
                margin-top: 2%;
                font-size: 1.2em;
                line-height: 1.4em;
                width: 500px;
              }

              @media @mobile-max768-landscape{
                margin-top: 2%;
                font-size: 0.8em;
                width: 306px;
                line-height: 18px;
              }
              @media @mobile-min568-max768{
                margin-top: 2%;
                font-size: 0.8em;
                line-height: 18px;
              }
              @media @mobile-max568-portrait{
                margin-top: 2%;
                font-size: 0.8em;
                line-height: 18px;
              }
              @media @mobile-max480{
                margin-top: 2%;
                font-size: 0.8em;
                line-height: 18px;
              }

              &:first-letter {
                text-transform: uppercase;
              }
            }
          }

          section.heading {
            color: #249fbe;
            text-transform: uppercase;
            font-family: 'Brandon Black';
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            flex: 2;
            align-items: center;
            height: 400px;
            justify-content: space-between;

            @media @mobile-min1920{
              font-size: 55.5px;
            }
            @media @mobile-max1920{
              font-size: 55.5px;
            }
            @media @mobile-max1440{
              font-size: 55.5px;
            }
            @media @mobile-max1366{
              font-size: 55.5px;
            }
            @media @mobile-max1280{
              font-size: 55.5px;
            }

            @media @mobile-max1024-landscape{
              font-size: 2.3em;
            }
            @media @mobile-max1024-portrait{
              font-size: 2.3em;
            }

            @media @mobile-max768-landscape{
              font-size: 55.5px;
            }
            @media @mobile-min568-max768{
              font-size: 1em;
            }
            @media @mobile-max568-portrait{
              font-size: 1em;
            }
            @media @mobile-max480{
              font-size: 1em;
              height: 34px;
            }

            @media only screen and (min-width: 320px) and (max-width: 374px) and (orientation: portrait) {
              font-size: 1em;
              height: 34px;
            }
            @media only screen and (min-width: 375px) and (max-width: 414px) and (orientation: portrait) {
              font-size: 1em;
              height: 118px;
            }
            @media only screen and (min-width: 414px) and (max-width: 480px) and (orientation: portrait){
              font-size: 1em;
              height: 153px;
            }
            div {

              @media @mobile-min1920{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 70px;
                line-height: 1.2em;
              }
              @media @mobile-max1920{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 70px;
                line-height: 1.2em;
              }
              @media @mobile-max1440{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 70px;
                line-height: 1.2em;
              }
              @media @mobile-max1366{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 70px;
                line-height: 1.2em;
              }
              @media @mobile-max1280{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 70px;
                line-height: 1.2em;
              }

              @media @mobile-max1024-landscape{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 70px;
                line-height: 1.2em;
              }
              @media @mobile-max1024-portrait{
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                margin-left: 70px;
                line-height: 1.2em;
              }

              @media @mobile-max768-landscape{
                display: -webkit-inline-box;
              }
              @media @mobile-min568-max768{
                display: -webkit-inline-box;
              }
              @media @mobile-max568-portrait{
                display: -webkit-inline-box;
              }
              @media @mobile-max480{
                display: -webkit-inline-box;
              }
            }
          }
        }
      }

      //.divider {
      //  flex: 0.2;
      //}

      .heading {
        color: #249fbe;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        display: flex;
        flex-direction: column;
        margin-top: 20px;
        flex: 2;

        .row {
          display: flex;
          flex-direction: row;
          width: 50%;

          .box {
            flex: 1;

            &.big-flex {

              @media @mobile-max1024-landscape{
                flex: 10;
              }
              @media @mobile-max1024-portrait{
                flex: 10;
              }
              @media @mobile-max768-landscape{
                flex: 0;
              }
              @media @mobile-min568-max768{
                flex: 0;
              }
              @media @mobile-max568-portrait{
                flex: 0;
              }
              @media @mobile-max480{
                flex: 0;
              }

              @media @mobile-min1920{
                flex: 3;
              }
              @media @mobile-max1920{
                flex: 3;
              }
              @media @mobile-max1440{
                flex: 3;
              }
              @media @mobile-max1366{
                flex: 3;
              }
              @media @mobile-max1280{
                flex: 3;
              }

            }

            &.text {
              flex: 10 0 auto;
            }

            &.align-right {
              text-align: right;
            }
          }
        }
      }
    }
  }

