.navigation-main {
  position: absolute;
  z-index: 3;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: flex-end;

  @media @mobile-min1920{
    padding: 40px 80px 40px 50px;
  }
  @media @mobile-max1920{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1440{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1366{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1280{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1280{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1024-landscape{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1024-portrait{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max768-landscape{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-min568-max768{
    padding: 16px 38px 16px 38px;
  }
  @media @mobile-max568-portrait{
    padding: 30px 80px 30px 30px;
  }
  @media @mobile-max480{
    padding: 20px;
  }

  .logo {
    width: 110px;
    height: 27px;
    display: block;
    background: url('../images/logo/logo.png') no-repeat 0 0;
    background-size: 100% 100%;
    animation: pulse 2s 1;

    @keyframes pulse {
      0% {
        background: url('../images/logo/logo.png') no-repeat 0 0;
        background-size: 100% 100%;
      }
      25% {
        background: url('../images/logo/logo-no-button.png') no-repeat 0 0;
        background-size: 100% 100%;
      }
      75% {
        background: url('../images/logo/logo.png') no-repeat 0 0;
        background-size: 100% 100%;
      }
      100% {
        background: url('../images/logo/logo-no-button.png') no-repeat 0 0;
        background-size: 100% 100%;
      }
    }

    @keyframes pulse-opened {
      0% {
        background-color: #fff;
      }
      25% {
        background: none;
      }
      75% {
        background-color: #fff;
      }
      100% {
        background: none;
      }
    }
  }

  .right-side {
    width: 100%;
  }

  .navigation-bars {
    display: none;
  }

  .languages {
    position: absolute;
    flex-direction: column;
    z-index: 2;
    display: none !important;

    &.visible {
      @media @mobile-max768-landscape{
        display: none;
      }
      @media @mobile-min568-max768{
        display: none;
      }
      @media @mobile-max1024-portrait{
        display: none;
      }
      @media @mobile-max568-portrait{
        display: none;
      }
      @media @mobile-max480{
        display: none;
      }
      @media @mobile-max480{
        display: none;
      }
      display: flex;
    }

    @media @mobile-min1920{
      right: 59px;
    }
    @media @mobile-max1920{
      right: 27px;
    }
    @media @mobile-max1440{
      right: 27px;
    }
    @media @mobile-max1366{
      right: 27px;
    }
    @media @mobile-max1024-landscape{
      right: 27px;
    }
    @media @mobile-max1024-portrait{
      right: 27px;
    }

    ul {
      li {
        margin: 5px 0;
        border: 1px solid #cadbe0;
        border-radius: 50%;
        padding: 7px 5px;
        font-size: 10px;
        text-align: center;
        text-transform: uppercase;
        background: #fff;

        &.active > a {
          font-weight: bold;
          color: #ff7d00;
        }

        a {
          text-decoration: none;
          color: #b5cfd3;
        }
      }
    }
  }

  .languages-mobile {
    display: none;
  }
}

.nav-box {
  position: fixed;
  text-align: center;
  overflow: hidden;
  z-index: -1;
  display: none;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #fff;

  ul {
    position: relative;
    top: 20%;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0;
    padding: 0;
    height: 50%;
    padding-left: 15px;
  }

  li {
    flex: 1;
    display: flex;
    align-items: center;
    font-family: 'Brandon Black';

    &:first-child {
      margin-bottom: 20px;
    }

    h1 {
      color: #1c9cbc;
      font-size: 3em;
    }

    a {
      flex: 1;
      text-decoration: none;
      font-size: 1.5em;
      text-transform: uppercase;
      color: #000;

      &:hover, &.active {
        color: #ff7d00;
      }
    }
  }
}

#toggle-nav-label {
  color: #000000;
  font-size: 16px;
  cursor: pointer;
  float: right;
  top: 5px;
  position: relative;
}

#toggle-nav {
  display: none;
}

#toggle-nav:checked {

  & ~ .nav-box {
    display: block;
    z-index: 400;
  }

}

#toggle-nav:checked ~ #toggle-nav-label {
  background: #fff;
  color: rgba(0, 0, 0, 0.8);
}

ul.nav {
  display: flex;
  list-style: none;
  justify-content: flex-end;
  padding: 0;
  li {
    a {
      padding: 0 26px;
      color: #131313;
      font-family: 'Brandon Black';
      text-transform: uppercase;
      font-size: 14px;
      opacity: .6;
      text-decoration: none;
      letter-spacing: 0.025em;

      &:hover, &.active {
        color: #ff7d00;
        opacity: 1;
      }
    }
  }
}

@media @mobile-max1024-portrait{
  .navigation-main {
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;

    .logo {
      width: 100px;
      height: 18px;
      background: url('../images/logo/logo.png') no-repeat 0 0;
      background-size: contain;
      animation: none;
    }

    .left-side {
      z-index: 500;
      flex: 1;
    }

    .navigation-bars {
      width: 90%;
      display: block;

      i {
        align-self: center;
        font-size: 24px;
      }
    }
    ul.nav {
      display: none;
      flex-direction: column;

      &.open {
        display: flex;
        position: absolute;
        margin: 0 auto;
        background: rgb(255, 255, 255, .3);
        width: 100%;
        left: 0;
        text-align: center;
        height: 100%;
        justify-content: center;

      }
    }
  }
}

