#blog {
  background: url('../images/backgrounds/blog.jpg');
  background-size: cover;
  background-color: #fff;
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @mobile-min1920{
    padding: 30px 100px 30px 50px;
  }
  @media @mobile-max1920{
    padding: 30px 80px 30px 50px;
  }
  @media @mobile-max1440{
    padding: 50px 50px 30px 50px;
  }
  @media @mobile-max1366{
    padding: 50px 60px 30px 50px;
  }
  @media @mobile-max1280{
    padding: 50px 50px 30px 50px;
  }

  @media @mobile-max1024-landscape{
    padding: 30px 70px 20px 50px;
  }
  @media @mobile-max1024-portrait{
    padding: 20px 20px 20px 20px;
  }

  @media @mobile-max768-landscape{
    padding: 10px 20px 10px 20px;
  }
  @media @mobile-min568-max768{
    padding: 10px 20px 10px 20px;
  }
  @media @mobile-max568-portrait{
    padding: 20px 20px 20px 20px;
  }
  @media @mobile-max480{
    padding: 20px 20px 20px 20px;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;
    flex-direction: column;

    @media @mobile-max768-landscape{
      padding: 0;
    }
    @media @mobile-min568-max768{
      padding: 0;
    }
    @media @mobile-max568-portrait{
      padding: 0;
    }
    @media @mobile-max480{
      padding: 0;
    }

    & > * {
      // flex: 1
    }

    .headline {
      color: #249fbe;
      text-transform: uppercase;
      font-family: 'Brandon Black';
      letter-spacing: 0.025em;
      margin-bottom: 0;

      @media @mobile-min1920{
        font-size: 80.5px;
        margin-bottom: 50px;
        margin-left: 290px;
      }
      @media @mobile-max1920{
        font-size: 80.5px;
        margin-bottom: 30px;
        margin-left: 150px;
      }
      @media @mobile-max1440{
        font-size: 80.5px;
        margin-bottom: 30px;
      }
      @media @mobile-max1366{
        font-size: 80.5px;
        margin-bottom: 30px;
      }
      @media @mobile-max1280{
        font-size: 80.5px;
        margin-bottom: 30px;
      }

      @media @mobile-max1024-landscape{
        font-size: 58px;
        margin-bottom: 30px;
      }
      @media @mobile-max1024-portrait{
        font-size: 80.5px;
      }

      @media @mobile-max768-landscape{
        font-size: 2em;
        margin-top: 45px;
      }
      @media @mobile-min568-max768{
        font-size: 2em;
        margin-top: 45px;
      }
      @media @mobile-max568-portrait{
        font-size: 50px;
        margin-top: 45px;
      }
      @media @mobile-max480{
        font-size: 2em;
        margin-top: 50px;
      }
    }

    .main-content {
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;
      height: 100%;

      @media @mobile-min1920{
        padding: 100px 20px 20px 20px;
      }
      @media @mobile-max1920{
        padding: 70px 20px 20px 20px;
      }
      @media @mobile-max1440{
        padding: 8px 20px 20px 20px;
      }
      @media @mobile-max1366{
        padding: 20px 20px 20px 20px;
      }
      @media @mobile-max1280{
        padding: 50px 20px 20px 20px;
      }

      @media @mobile-max1024-landscape{
        padding: 60px 20px 20px 20px;
      }
      @media @mobile-max1024-portrait{
        padding: 40px 20px 0 20px;

      }

      @media @mobile-max768-landscape{
        padding: 0;
      }
      @media @mobile-min568-max768{
        padding: 0;
      }
      @media @mobile-max568-portrait{
        padding: 0;
      }
      @media @mobile-max480{
        padding: 0;
      }

      & > .content {
        display: flex;
        justify-content: space-around;
        align-items: center;
        overflow: hidden;
        position: relative;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;

        @media @mobile-min1920{
          height: 35em;
        }
        @media @mobile-max1920{
          height: 35em;
        }
        @media @mobile-max1440{
          height: 35em;
        }
        @media @mobile-max1366{
          height: 35em;
        }
        @media @mobile-max1280{
          height: 35em;
        }

        @media @mobile-max1024-landscape{
          height: 35em;
        }
        @media @mobile-max1024-portrait{
          height: 56em;
          overflow-y: scroll;
        }

        @media @mobile-max768-landscape{
          overflow-y: scroll;
        }
        @media @mobile-min568-max768{
          overflow-y: scroll;
        }
        @media @mobile-max568-portrait{
          overflow-y: scroll;
        }
        @media @mobile-max480{
          overflow-y: scroll;
        }

        article {
          display: flex;
          align-items: flex-start;
          flex-shrink: 0;

          @media @mobile-min1920{
            width: 80%;
          }
          @media @mobile-max1920{
            width: 80%;
          }
          @media @mobile-max1440{
            width: 80%;
          }
          @media @mobile-max1366{
            width: 90%;
          }
          @media @mobile-max1280{
            width: 90%;
          }

          @media @mobile-max1024-landscape{
            width: 100%;
          }
          @media @mobile-max1024-portrait{
            width: 100%;
            flex-direction: column;
          }

          @media @mobile-max768-landscape{
            width: 100%;
            flex-direction: column;
          }
          @media @mobile-min568-max768{
            width: 100%;
            flex-direction: column;
          }
          @media @mobile-max568-portrait{
            width: 100%;
            flex-direction: column;
          }
          @media @mobile-max480{
            width: 100%;
            flex-direction: column;
          }

          &:first-child {
            padding-bottom: 30px;
          }

          &:not(:first-child) {
            padding: 60px 0;

            @media @mobile-max1024-portrait{
              padding: 0 15px;
            }

            @media @mobile-max768-landscape{
              padding: 0 15px;
            }
            @media @mobile-min568-max768{
              padding: 0 15px;
            }
            @media @mobile-max568-portrait{
              padding: 0 15px;
            }
            @media @mobile-max480{
              padding: 0 15px;
            }

          }

          .header-mobile {

            @media @mobile-min1920{
              display: none;
            }
            @media @mobile-max1920{
              display: none;
            }
            @media @mobile-max1440{
              display: none;
            }
            @media @mobile-max1366{
              display: none;
            }
            @media @mobile-max1280{
              display: none;
            }

            @media @mobile-max1024-landscape{
              display: none;
            }
            @media @mobile-max1024-portrait{
              display: flex;
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 34px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;
              a {
                color: #009ec0;
                text-decoration: none;
              }
            }

            @media @mobile-max768-landscape{
              display: flex;
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 18px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;
              a {
                color: #009ec0;
                text-decoration: none;
              }
            }
            @media @mobile-min568-max768{
              display: flex;
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 18px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;
              a {
                color: #009ec0;
                text-decoration: none;
              }
            }
            @media @mobile-max568-portrait{
              display: flex;
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 14px;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;
              a {
                color: #009ec0;
                text-decoration: none;
              }
            }
            @media @mobile-max480{
              display: flex;
              color: #009ec0;
              font-family: 'Brandon Bold';
              font-size: 1.8em;
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;

              a {
                color: #009ec0;
                text-decoration: none;
              }
            }
          }

          .image {

            @media @mobile-min1920{

            }
            @media @mobile-max1920{

            }
            @media @mobile-max1440{

            }
            @media @mobile-max1366{

            }
            @media @mobile-max1280{

            }

            @media @mobile-max1024-landscape{

            }

            @media @mobile-max1024-portrait{
              flex: 1;
              margin: 0 auto;
            }

            @media @mobile-max768-landscape{
              flex: 1;
            }
            @media @mobile-min568-max768{
              flex: 1;
            }
            @media @mobile-max568-portrait{
              flex: 1;
            }
            @media @mobile-max480{
              flex: 1;
            }

            img {
              @media @mobile-min1920{

              }
              @media @mobile-max1920{

              }
              @media @mobile-max1440{

              }
              @media @mobile-max1366{

              }
              @media @mobile-max1280{

              }

              @media @mobile-max1024-landscape{

              }
              @media @mobile-max1024-portrait{
                width: 100%;
              }

              @media @mobile-max768-landscape{
                width: 100%;
              }
              @media @mobile-min568-max768{
                width: 100%;
              }
              @media @mobile-max568-portrait{
                width: 100%;
              }
              @media @mobile-max480{
                width: 100%;
              }
            }
          }

          .content {
            display: flex;
            padding: 0;

            a {
              color: #009ec0;
              text-decoration: none;

              //@media @mobile {
              //  margin-left: 10px;
              //}

              &:hover {
                color: #009ec0;
                text-decoration: underline;
              }
            }

            @media @mobile-min1920{
              margin-left: 130px;
            }
            @media @mobile-max1920{
              margin-left: 130px;
            }
            @media @mobile-max1440{
              margin-left: 130px;
            }
            @media @mobile-max1366{
              margin-left: 100px;
            }
            @media @mobile-max1280{
              margin-left: 100px;
            }

            @media @mobile-max1024-landscape{
              margin-left: 10px;
            }
            @media @mobile-max1024-portrait{
              //  flex: 1;
              //  margin: 0;
            }

            @media @mobile-max768-landscape{
              //  flex: 1;
              //  margin: 0;
            }
            @media @mobile-min568-max768{
              //  flex: 1;
              //  margin: 0;
            }
            @media @mobile-max568-portrait{
              //  flex: 1;
              //  margin: 0;
            }
            @media @mobile-max480{
              //  flex: 1;
              //  margin: 0;
            }

            h4 {
              color: #009ec0;
              font-family: 'Brandon Bold';
              margin: 0;
              align-self: flex-start;
              text-transform: uppercase;
              line-height: 1;


              @media @mobile-min1920{
                font-size: 34px;
              }
              @media @mobile-max1920{
                font-size: 34px;
              }
              @media @mobile-max1440{
                font-size: 34px;
              }
              @media @mobile-max1366{
                font-size: 34px;
              }
              @media @mobile-max1280{
                font-size: 34px;
              }

              @media @mobile-max1024-landscape{
                font-size: 34px;
              }
              @media @mobile-max1024-portrait{
                font-size: 14px;
                display: none;
              }

              @media @mobile-max768-landscape{
                font-size: 14px;
                display: none;
              }
              @media @mobile-min568-max768{
                font-size: 14px;
                display: none;
              }
              @media @mobile-max568-portrait{
                font-size: 14px;
                display: none;
              }
              @media @mobile-max480{
                font-size: 14px;
                display: none;
              }
            }

            p {
              font-family: 'Proxima Nova';
              color: #525a5d;

              //@media @mobile {
              //  font-size: 14px;
              //}
            }

            .footer {
              display: flex;
              font-family: 'Proxima Nova';
              font-size: 17px;
              flex: 1;
              justify-content: space-between;
              width: 200px;
              align-self: baseline;

              //@media @mobile {
              //  flex-direction: row;
              //  font-size: 12px;
              //  justify-content: flex-start;
              //}

              .date {
                color: #b1ccd2;
              }

              a {
                color: #1f1f1f;

                //@media @mobile {
                //  margin-left: 10px;
                //}

                &:hover {
                  color: #ff7d00;
                }
              }
            }
          }
        }
      }
    }
  }
}
