#index {
  height: 100%;
  background-size: cover;
  background-color: #eff8f9;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;

  @media @mobile-min1920{
    background: url('../images/backgrounds/index.jpg');
    padding: 30px 100px 30px 50px;
  }
  @media @mobile-max1920{
    background: url('../images/backgrounds/index.jpg');
    padding: 30px 80px 30px 50px;
    background-position: 59% 0;
    background-size: 112%;
  }
  @media @mobile-max1440{
    background: url('../images/backgrounds/index.jpg');
    padding: 30px 80px 30px 50px;
    background-position: 59% 0;
    background-size: 112%;
  }
  @media @mobile-max1366{
    background: url('../images/backgrounds/index.jpg');
    padding: 30px 60px 30px 50px;
    background-position: 59% 0;
    background-size: 136%;
  }
  @media @mobile-max1280{
    background: url('../images/backgrounds/index.jpg');
    padding: 30px 80px 30px 50px;
    background-position: 59% 0;
    background-size: 136%;
  }

  @media @mobile-max1024-landscape{
    background: url('../images/backgrounds/index.jpg');
    padding: 30px 70px 20px 50px;
    background-position: 59% 0;
    background-size: 136%;
  }
  @media @mobile-max1024-portrait{
    background: url('../images/backgrounds/index.jpg');
    background-position: 38% 0;
    background-size: 239%;
    padding: 20px 20px 20px 20px;
  }
  @media @mobile-max768-landscape{
    background: url('../images/backgrounds/index_1136_640.jpg');
    padding: 20px 20px 20px 20px;
    background-position: 61% 52%;
    background-size: 129%;
  }
  @media @mobile-min568-max768{
    background: url('../images/backgrounds/index_1136_640.jpg');
    padding: 10px 20px 10px 20px;
    background-position: 61% 52%;
    background-size: 129%;
  }
  @media @mobile-max568-portrait{
    background: url('../images/backgrounds/index_640_1136.jpg');
    padding: 20px 20px 20px 20px;
    background-position: 0 0;
    background-size: 100%;
  }
  @media @mobile-max480{
    background: url('../images/backgrounds/index_640_1136.jpg');
    padding: 20px 20px 20px 20px;
    background-position: 0 0;
    background-size: 102%;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;
    padding-top: 10vh;

    .main-content {
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: stretch;
      flex: 1;

      //.divider {
      //  flex: 0.2;
      //}

      .heading {
        color: #249fbe;
        letter-spacing: 0.025em;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        flex: 2;

        @media @mobile-min1920{
          font-size: 80.5px;
        }
        @media @mobile-max1920{
          font-size: 80.5px;
        }
        @media @mobile-max1440{
          font-size: 80.5px;
        }
        @media @mobile-max1366{
          font-size: 80.5px;
        }
        @media @mobile-max1280{
          font-size: 80.5px;
        }

        @media @mobile-max1024-landscape{
          font-size: 68px;
        }
        @media @mobile-max1024-portrait{
          font-size: 68px;
        }

        .row {
          display: flex;
          flex-direction: row;


          @media @mobile-min1920{
            height: 55px;
          }
          @media @mobile-max1920{
            height: 55px;
          }
          @media @mobile-max1440{
            height: 55px;
          }
          @media @mobile-max1366{
            height: 55px;
          }
          @media @mobile-max1280{
            height: 55px;
          }

          @media @mobile-max1024-landscape{
            height: 55px;
          }
          @media @mobile-max1024-portrait{

          }

          @media @mobile-max768-landscape{
            height: 55px;
          }
          @media @mobile-min568-max768{
            height: 55px;
          }
          @media @mobile-max568-portrait{
            height: 55px;
          }
          @media @mobile-max480{
            height: 55px;
          }

          &:first-child {
            //margin-top: 100px;
            //padding-left: 110px;

            @media @mobile-max768-landscape{
              margin-top: 15%;
              padding: 0;
            }
            @media @mobile-min568-max768{
              margin-top: 15%;
              padding: 0;
            }
            @media @mobile-max568-portrait{
              margin-top: 92%;
              padding: 0;
            }
            @media @mobile-max480{
              margin-top: 92%;
              padding: 0;
            }

          }

          .box {
            flex: 1;

            &.big-flex {
              flex: 3;
            }

            &.text {
              flex: 10 0 auto;
            }

            &.align-right {
              text-align: right;
            }
          }
        }
      }
    }
  }
}

.side-nav {
  position: absolute;
  top: 0;
  right: 40px;
  height: 100%;
  border-left: 1px solid #cadbe0;
}

@media @mobile-min1920{
  #index {
    .content .main-content {
      .heading {

        .row {
          width: 50%;
          &:first-child {
            margin-top: 210px;
            padding-left: 112px;
          }

          &:nth-child(2n) {
            margin-top: 28px;
            margin-right: 31px;
          }
        }
      }

    }
  }
}

@media @mobile-max1920{
  .content .main-content {
    .heading {

      .row {
        &:first-child {
          margin-top: 190px;
          padding-left: 112px;
        }

        &:nth-child(2n) {
          margin-top: 28px;
          margin-right: 31px;
        }
      }
    }

  }

}

