        }
      }
    }

    .footer {
      flex: 0.3;

      .copyright {
        font-size: 12px;
      }

      .bottom {
        .box {
          &:first-child {
            margin-right: 12px;
          }
          &:nth-child(2) {
            margin-right: 10px;
          }
          &:nth-child(3) {
            margin-right: 21px;
          }
          &:nth-child(4) {
            margin: 0;
          }
        }
      }

      .cta {
        font-size: 11px;
        text-transform: uppercase;
        margin-right: 76px;
      }
    }
  }
}

@media @mobile-max1024-landscape{
  .heading {
    .row {
      width: 50%;

      &:first-child {
        margin-top: 180px;
        padding-left: 112px;
      }

      &:nth-child(2n) {
