#contact {
  background: url('../images/backgrounds/contact.jpg');
  background-size: cover;
  background-color: #fff;
  background-repeat: no-repeat;
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;

  @media @mobile-min1920{
    padding: 30px 100px 30px 50px;
  }
  @media @mobile-max1920{
    padding: 30px 80px 30px 50px;
    background-position: 54% 0;
    background-size: 115%;
  }
  @media @mobile-max1440{
    padding: 50px 50px 30px 50px;
    background-position: 54% 0;
    background-size: 115%;
  }
  @media @mobile-max1366{
    padding: 50px 60px 30px 50px;
    background-position: 39% 0;
    background-size: 135%;
  }
  @media @mobile-max1280{
    padding: 50px 50px 30px 50px;
    background-position: 54% 0;
    background-size: 146%;
  }

  @media @mobile-max1024-landscape{
    padding: 30px 70px 20px 50px;
    background-position: 54% 0;
    background-size: 146%;
  }
  @media @mobile-max1024-portrait{
    padding: 20px 20px 20px 20px;
    background-position: 45% 174px;
    background-size: 216%;
  }

  @media @mobile-max768-landscape{
    padding: 10px 20px 10px 20px;
  }
  @media @mobile-min568-max768{
    padding: 10px 20px 10px 20px;
  }
  @media @mobile-max568-portrait{
    padding: 20px 20px 20px 20px;
    background-position: 43% 201px;
    background-size: 239%;
  }
  @media @mobile-max480{
    padding: 20px 20px 20px 20px;
    background-position: 43% 201px;
    background-size: 239%;
  }

  .left-side {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  .content {
    height: 100%;
    display: flex;
    flex-direction: column;

    .heading {
      display: flex;

      .counterweight {
        &:first-child {
          flex: 1.5;
        }

        &:last-child {
          flex: 1;
        }

        @media @mobile-max1024-portrait{
          display: none;
        }

        @media @mobile-max768-landscape{
          //display: none;
        }
        @media @mobile-min568-max768{
          //display: none;
        }
        @media @mobile-max568-portrait{
          display: none;
        }
        @media @mobile-max480{
          display: none;
        }
      }

      .headline {
        flex: 4;
        color: #249fbe;
        text-transform: uppercase;
        font-family: 'Brandon Black';
        margin-bottom: 0;

        @media @mobile-min1920{
          font-size: 80.5px;
          margin-left: 568px;
          margin-top: 158px;
        }
        @media @mobile-max1920{
          font-size: 70px;
          margin-left: 360px;
          margin-top: 130px;
        }
        @media @mobile-max1440{
          font-size: 70px;
          margin-left: 284px;
          margin-top: 74px;
        }
        @media @mobile-max1366{
          font-size: 60px;
          margin-left: 258px;
          margin-top: 88px;
        }
        @media @mobile-max1280{
          font-size: 80.5px;
        }

        @media @mobile-max1024-landscape{
          font-size: 52px;
          margin-left: 62px;
          margin-top: 104px;
        }
        @media @mobile-max1024-portrait{
          font-size: 80.5px;
          margin-left: 20px;
        }

        @media @mobile-max768-landscape{
          font-size: 2em;
          margin-top: 45px;
        }
        @media @mobile-min568-max768{
          font-size: 1em;
          margin-top: 45px;
          margin-left: 174px;
        }
        @media @mobile-max568-portrait{
          font-size: 50px;
          margin-top: 45px;
        }
        @media @mobile-max480{
          font-size: 0.8em;
          margin-top: 50px;
        }
      }
    }

    .main-content {
      align-items: center;
      display: flex;
      flex-direction: column;
      //flex: 1;

      @media @mobile-min1920{
        margin-left: 30px;
        margin-top: 50px;
      }
      @media @mobile-max1920{
        margin-left: 30px;
        margin-top: 50px;
      }
      @media @mobile-max1440{
        margin-top: 20px;
      }
      @media @mobile-max1366{
        margin-top: 10px;
      }

      @media @mobile-max1280{
        margin-top: 20px;
      }

      @media @mobile-max1024-landscape{
        margin-top: 20px;
      }
      @media @mobile-max1024-portrait{
        margin-top: 50px;
      }

      @media @mobile-max768-landscape{
        //margin-top: 50px;
      }
      @media @mobile-min568-max768{
        margin-top: 10px;
      }
      @media @mobile-max568-portrait{
        margin-top: 50px;
      }
      @media @mobile-max480{
        margin-top: 50px;
      }

      .content {
        display: flex;

        @media @mobile-max1024-portrait{
          position: relative;
          left: 50px;
        }

        @media @mobile-max768-landscape{
          position: relative;
          left: 50px;
        }
        @media @mobile-min568-max768{
          position: relative;
          left: 80px;
        }
        @media @mobile-max568-portrait{
          position: relative;
          left: 50px;
        }
        @media @mobile-max480{
          position: relative;
          left: 36px;
        }

        & > * {
        }

        .left-side {

          @media @mobile-max1024-portrait{
            display: none;
          }

          @media @mobile-max768-landscape{
            display: none;
          }
          @media @mobile-min568-max768{

          }
          @media @mobile-max568-portrait{
            display: none;
          }
          @media @mobile-max480{
            display: none;
          }
        }

        .right-side {

          @media @mobile-min1920{
            margin-left: 460px;
          }
          @media @mobile-max1920{
            margin-left: 398px;
          }
          @media @mobile-max1440{
            margin-left: 398px;
          }
          @media @mobile-max1366{
            margin-left: 398px;
          }
          @media @mobile-max1280{
            margin-left: 286px;
          }

          @media @mobile-max1024-landscape{
            margin-left: 344px;
          }
          @media @mobile-max1024-portrait{
            margin-left: 100px;
          }

          @media @mobile-max768-landscape{

          }
          @media @mobile-min568-max768{

          }
          @media @mobile-max568-portrait{

          }
          @media @mobile-max480{

          }

