              a {
                font-family: 'Proxima Nova';
                color: #009ec0;
                text-transform: uppercase;
                text-decoration: none;
                font-weight: bold;
                letter-spacing: 0.025em;

                @media @mobile-min1920{
                  font-size: 24px;
                  margin-left: 30px;
                }
                @media @mobile-max1920{
                  font-size: 24px;
                  margin-left: 30px;
                }
                @media @mobile-max1440{
